export const pageQuery = graphql`
  query {
    file(relativePath: { eq: "gatsby-explanation.png" }) {
      childImageSharp {
        fluid(maxWidth: 870) {
          src
          srcSet
          sizes
        }
      }
    }
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date, fields___slug] }
      limit: 4
      filter: {
        frontmatter: { draft: { ne: true } }
        fileAbsolutePath: { regex: "/docs.blog/" }
        fields: { released: { eq: true } }
      }
    ) {
      edges {
        node {
          ...HomepageBlogPostData
        }
      }
    }
    
    # allImages(
    #   filter:{
    #     extension: {regex :"/(JPG)/"},
    #     relativeDirectory: {eq: "images"}
    #   }
    # ){
    #   edges{
    #     node{
    #       childImageSharp{
    #         fluid(maxWidth: 1024, maxHeight: 876) {
    #           srcSet
    #         }  
    #       }
    #     }
    #   }
    # }
   
    imageOne: file(relativePath: { eq: "all.JPG" }) {
      childImageSharp {
        fluid(maxWidth: 3000) {
          srcSet
          originalImg
        }
      }
    }
    imageTwo: file(relativePath: { eq: "bathroom.JPG" }) {
      childImageSharp {
        fluid(maxWidth: 3000) {
          srcSet
        }
      }
    }
    bedroom: file(relativePath: { eq: "bedroom.JPG" }) {
      childImageSharp {
        fluid(maxWidth: 3000) {
          srcSet
        }
      }
    }
    entry: file(relativePath: { eq: "entry.JPG" }) {
      childImageSharp {
        fluid(maxWidth: 3000) {
          srcSet
        }
      }
    }
    shower: file(relativePath: { eq: "shower.JPG" }) {
      childImageSharp {
        fluid(maxWidth: 3000) {
          srcSet
        }
      }
    }
  }
`
